import Vue from 'vue';
import Vuetify from 'vuetify/lib';
import magnify from './../components/icons/magnify';
import instagram from './../components/icons/instagram';
import telegram from './../components/icons/telegram';
import gmail from './../components/icons/gmail';
import github from './../components/icons/github';
import linkedin from './../components/icons/linkedin';
import moon from './../components/icons/moon';
import sun from './../components/icons/sun';
import show from './../components/icons/show';
import bag from './../components/icons/bag';
import user from './../components/icons/user';
import list from './../components/icons/list';
import award from './../components/icons/award';
import language from './../components/icons/language';
import education from './../components/icons/education';
import '@mdi/font/css/materialdesignicons.css';

Vue.use(Vuetify);

export default new Vuetify({
  theme: {
    themes: {
      light: {
        primary: '#455A64',
        secondary: '#406882',
        accent: '#8c9eff',
        error: '#b71c1c',
        info: '#1976D2',
        greyColor: '#4a4a4a',
        bgColor: '#f0f0f0',
        bgGrey: '#EEEEEE',
        mainFont: '#000000',
      },
      dark: {
        primary: '#263238',
        secondary: '#9D5353',
        accent: '#8c9eff',
        error: '#b71c1c',
        info: '#1976D2',
        greyColor: 'b9b6b6',
        bgColor: '#121212',
        bgGrey: '#424242',
        mainFont: '#fff',
      },
    },
  },
  icons: {
    values: {
      magnify: { component: magnify },
      instagram: { component: instagram },
      telegram: { component: telegram },
      gmail: { component: gmail },
      github: { component: github },
      linkedin: { component: linkedin },
      moon: { component: moon },
      sun: { component: sun },
      show: { component: show },
      user: { component: user },
      bag: { component: bag },
      list: { component: list },
      award: { component: award },
      language: { component: language },
      education: { component: education },
    },
  },
});
